import React from "react";
import Layout from "../layouts";
import { graphql } from "gatsby";
import Car from "../components/Car";
import Pagination from "../components/Pagination";
import {Helmet} from "react-helmet";

export default function index(props) {
  const { data, pageContext } = props;
  const { currentPage, numPages } = pageContext;
  const pagination = {
    currentPage,
    numPages,
    path: "cars",
  };
  return (
    <Layout>
      <Helmet>
        <title>Cars</title>
      </Helmet>
      
      <div className="page-heading about-heading header-text">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="text-content">
                <h4>Lorem ipsum dolor sit amet</h4>
                <h2>Fleet</h2>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="latest-products inner-page">
        <div className="container">
          <div className="row">
            {data.allContentfulCar.nodes.map((item, index) => {
              return (
                <div className="col-md-4" key={`car_${index}`}>
                  <Car item={item} />
                </div>
              );
            })}
          </div>
          <Pagination {...pagination} />
        </div>
      </div>
    </Layout>
  );
}
export const carListQuery = graphql`
  query carListQuery($skip: Int!, $limit: Int!) {
    allContentfulCar(
      sort: { fields: [createdAt], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      nodes {
        description {
          description
        }
        id
        brand
        fuel
        image {
          gatsbyImageData
        }
        name
        offer
        price
        seats
        slug
        year
        transmission
      }
    }
  }
`;
